/*
 *   Progress Bar Thickness
 */

/*.circle-progress:before,*/
.circle-progress .bar,
.circle-progress .fill
{
    border: 2px solid;
}



.circle-progress
{
    position: relative;
    width: 100px;
    height: 100px;
    padding-top: 100px;
    border-radius: 50%;
    display: inline-block;
    margin: 0 20px;
    cursor: default;
}


